import React, { Component } from "react";
import ReactApexChart from "react-apexcharts";
import $ from 'jquery'
import { COPY_TO_CLIPBOARD } from "../../../../../../Services/Utils/utils";

class GetRightsPendingAndExpire extends Component {
  constructor(props) {
    super(props);
    this.state = {
      codeCopied: false,
      series: [this.props.days * 100 / 30],
      options: {
        chart: {
          type: 'radialBar',
          toolbar: {
            show: false
          },
          animations: {
            enabled: true,
            easing: 'easeinout',
            speed: 1000,
            animateGradually: {
              enabled: true,
              delay: 500
            },
            dynamicAnimation: {
              enabled: true,
              speed: 3000
            }
          },
        },
        colors: ['#FF8100'],
        plotOptions: {
          radialBar: {
            startAngle: -135,
            endAngle: 225,
            hollow: {
              margin: 0,
              size: '70%',
              background: '#fff',
              image: undefined,
              imageOffsetX: 0,
              imageOffsetY: 0,
              position: 'front',
              dropShadow: {
                enabled: true,
                top: 3,
                left: 0,
                blur: 4,
                opacity: 0.1
              }
            },
            track: {
              background: '#fff',
              strokeWidth: '90%',
              margin: 0, // margin is in pixels
              dropShadow: {
                enabled: true,
                top: -3,
                left: 0,
                blur: 5,
                opacity: 0.25
              }
            },

            dataLabels: {
              show: true,
              name: {
                offsetY: 0,
                show: true,
                fontSize: '24px',
                fontWeight: '600'
              },
              value: {
                formatter: function () {
                  return 'Remaining';
                },
                color: '#111',
                fontSize: '14px',
                show: true,
              }
            }
          }
        },
        stroke: {
          lineCap: 'round'
        },
        labels: [this.props.days + ' Days'],
      },


    };
  }
  codeCopy = (id) => {
    const { modalAssetData } = this.props
    this.setState({
      codeCopied: true
    })
    setTimeout(() => {
      this.setState({
        codeCopied: false
      })
    }, 1500);
    COPY_TO_CLIPBOARD(id);
    navigator.clipboard.writeText($('#custom_message').val() + ' Post : ' + modalAssetData.link + ' Form Link : ' + $('#ugc_rights_url_').val());
    if (modalAssetData.networkId == 18 || modalAssetData.networkId == 2) setTimeout(() => window.open(`https://ig.me/m/${modalAssetData.username}`, '_blank'), 400)
    else setTimeout(() => window.open(modalAssetData.link, '_blank'), 400)

  }
  render() {
    const { expireStatus, modalAssetData } = this.props
    const { codeCopied } = this.state
    return <div className="p-lg-6 p-3 pt-0 pt-lg-2">
      <div className="mb-0">
        <ReactApexChart options={this.state.options} series={this.state.series} type="radialBar" height={300} />
      </div>
      <div className="mb-6">
        <h5 className="mb-2 pt-1 text-center">Your UGC Request Sharing URL</h5>
        <p className="text-muted text-center">Copy and paste link in the comments or original post</p>
        <div className="mb-6">
          <textarea type="text" id="custom_message" className="form-control form-control-lg textarea-min-row-3 bg-light" rows={3} placeholder="Add Comment" defaultValue={modalAssetData.ugcRights.custom_message} readOnly />
        </div>
        <div className="mb-6">
          <div className="input-group mb-4">
            <div className="input-group-text">
            <i className="ri-link icon-4"></i>
            </div>
            <input type="text" id="ugc_rights_url_" className="form-control form-control-lg" defaultValue={modalAssetData.ugcRights.bitlyurl} readOnly />
          </div>
          <button type="button" className="btn btn-primary btn-lg w-100" onClick={() => { this.codeCopy('ugc_rights_url_') }}>
            {codeCopied ? 'Copied' : 'Copy URL'}
          </button>
        </div>
        <div className="mb-6">
          <h6>Steps to proceed</h6>
          <ul className="list-unstyled list-points fs-7">
            <li className="mb-3">You have successfully generated a "UGC Request" for this post.</li>
            <li className="mb-3">Quickly review your Request before sending it to the owner.</li>
            <li className="mb-3">Click on the "Generate Request" button located in the right bottom corner of the screen and then you will be redirected to the user's original post.</li>
            <li className="mb-3">Paste the link of "UGC Request" in the comments section of the original post.</li>
            <li className="mb-3">You will get UGC Rights asap post owner accept your request and allow you to use their post.</li>
          </ul>
        </div>
      </div>
    </div>
  }
}

export default GetRightsPendingAndExpire;